import React, { PureComponent } from 'react';
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { DetailWrapper, Header, Content } from "./style";
import { action } from "./store";

class Detail extends PureComponent {
  componentDidMount() {
    const id = this.props.match.params.id  //获取路由参数
    this.props.getDetail(id)
  }

  render() {
    const { detail } = this.props
    return (
      <div>
        <DetailWrapper>
          <Header>{detail.get('title')}</Header>
          <Content dangerouslySetInnerHTML={{ __html: detail.get('content') }}></Content>   {/* 转义 dangerouslySetInnerHTML={{__html: 内容}} */}
        </DetailWrapper>
      </div>
    );
  }

}

const mapState = (state) => ({
  detail: state.getIn(['detail','data'])
})

const mapDispatch = (dispatch,props) => ({
  getDetail(id) {  
    dispatch(action.GetDetail(id))
  }
})

export default connect(mapState,mapDispatch)(withRouter(Detail));